body { 
  background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', sans-serif;
}

.navbar {
  background: #131313;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar__container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 50px;
}

#navbar__logo {
  background-color: #c8358e;
  background-image: linear-gradient(to top, #a8146d 0%, #f09acd 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  font-size: 3rem;
  font-family: 'Courgette', cursive;
  
}

.fa-gem {
  margin-right: 0.5rem;
}

.navbar__menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
}

.navbar__item {
  height: 80px;
}

.navbar__links {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
}

.navbar__links:hover {
  color: #c8358e;
  transition: all 0.3s ease;
}

@media screen and (max-width: 960px) {
  .navbar__container {
      display: flex;
      justify-content: space-between;
      height: 80px;
      z-index: 1;
      width: 100%;
      max-width: 1300px;
      padding: 0;
  }

  .navbar__menu {
      display: grid;
      grid-template-columns: auto;
      margin: 0;
      width: 100%;
      position: absolute;
      top: -1000px;
      opacity: 0;
      transition: all 0.5s ease;
      height: 50vh;
      z-index: -1;
      background: #131313;
  }

  .navbar__menu.active {
      background: #131313;
      top: 100%;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 99;
      height: 50vh;
      font-size: 1.6rem;
  }

  #navbar__logo {
      padding-left: 25px;
  }

  .navbar__toggle .bar {
      width: 25px;
      height: 3px;
      margin: 5px auto;
      transition: all 0.3s ease-in-out;
      background: #fff;
  }

  .navbar__item {
      width: 100%;    
  }

  .navbar__links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
  }

  #mobile-menu {
      position: absolute;
      top: 20%;
      right: 5%;
      transform: translate(5%, 20%);
  }

  .navbar__toggle .bar {
      display: block;
      cursor: pointer;
  }

  #mobile-menu.is-active .bar:nth-child(2) {
      opacity: 0;
  }

  #mobile-menu.is-active .bar:nth-child(1) {
      transform: translate(8px) rotate(45deg);
  }

  #mobile-menu.is-active .bar:nth-child(3) {
      transform: translate(-8px) rotate(-45deg);
  }
}

/* Hero Section CSS*/
.main {
  background-color: #181818;
}

.main__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-self: center;
  margin: 0 auto;
  height: 90vh;
  background-color: #3b3b3b;
  z-index: 1;
  width: 100%;
  max-width: 1300px;  
  padding: 0 50px;
}

.main__content h1 {
  font-family: 'Courgette', cursive;
  font-size: 5.5rem;
  background-color: #c8358e;
  background-image: linear-gradient(to top, #a8146d 0%, #f09acd 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.main__content h2 {
  font-size: 4rem;
  background-color: #e3dc2c;
  background-image: linear-gradient(to top, #e3dc2c 0%, #f3f0a7 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.main__content p {
  font-family: 'Work Sans', sans-serif;
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
}

.main__content h3 {
  font-size: 2rem;
  color: #e9e9e9;
  margin: 2rem;
}

.main__content ul {
  color: #e3dc2c;
  margin: 1rem;
  font-size: 1.5rem;
}

.main__img--container {
  text-align: center;
}

#main__img {
  height: 80%;
  width: 80%;
}

/* Responsive */
@media screen and (max-width: 768px) {
  .main__container {
      display: grid;
      grid-template-columns: auto;
      align-items: center;
      justify-self: center;
      width: 100%;
      margin: 0 auto;
      height: 90vh;
  }

  .main__content {
      text-align: center;
      margin-bottom: 4rem;
  }

  .main__content h1 {
      font-size: 5.5rem;
      margin-top: 2rem;
  }

  .main__content h2 {
      font-size: 3rem;
  }

  .main__content p {
      margin-top: 1rem;
      font-size: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .main__content h1 {
      font-size: 4rem;
      margin-top: 3rem;
  }

  .main__content h2 {
      font-size: 2rem;
  }

  .main__content p {
      margin-top: 2rem;
      font-size: 1.5rem;
  }
}

/* Services CSS */
.services {
  background: #252525;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.services h1 {
  background-color: #e3dc2c;
  background-image: linear-gradient(to right, #e3dc2c 0%, #f3f0a7 0%, #dbd528 100%);
  background-size: 100%;
  margin-top: 4rem;
  margin-bottom: 5rem;
  font-size: 2.5rem;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.services__container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.services__card {
  margin: 1rem;
  height: 525px;
  width: 400px;
  border-radius: 4px;
  background-image: linear-gradient(
      to bottom,
      rgba(17,17,17,0.6) 100%, 
      rgba(0,0,0,0) 0%), 
      url('./pages/images/pic3.jpg');
  background-size: cover;
  position: relative;
  color: #fff;
}

.services__card:nth-child(2) {
  background-image: linear-gradient(
      to bottom,
      rgba(17,17,17,0.6) 100%, 
      rgba(0,0,0,0) 0%), 
      url('./pages/images/pic4.jpg');
}

.services h2 {
  position: absolute;
  top: 350px;
  left: 30px;
}

.services__card p {
  position: absolute;
  top: 400px;
  left: 30px;
}

.services__card:hover {
  transform: scale(1.075);
  transition: 0.2s ease-in;
  cursor: pointer;
}

@media screen and (max-width: 960px) {
  .services {
      height: 1400px;
  }

  .services h1 {
      font-size: 2rem;
      margin-top: 6rem;
  }
}

@media screen and (max-width: 480px) {
  .services {
      height: 1400px;
  }

  .services h1 {
      font-size: 1.2rem;
  }

  .services__card {
      width: 300px;
  }
}

/* Footer CSS */
.footer__container {
  background-color: #131313;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#footer__logo {
  color: #fff;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  font-family: 'Courgette', cursive;
}

.footer__links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.footer__link-wrapper {
  display: flex;
}

.footer__link--items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.footer__link--items h2 {
  margin-bottom: 16px;
}

.footer__link--items > h2 {
  color: #fff;
}

.footer__link--items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer__link--items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

/* Social Icons */
.social__icon--link {
  color: #fff;
  font-size: 24px;

}

.social__media {
  max-width: 1000px;
  width: 100%;

}

.social__media--wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social__icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social__logo {
  color: #fff;
  justify-self: flex-start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website__rights {
  color: #fff;
}

@media screen and (max-width: 820px) {
  .footer__links {
      padding-top: 2rem;
  }

  #footer__logo {
      margin-bottom: 2rem;
  }

  .website__rights {
      margin-bottom: 2rem;
  }

  .footer__link--wrapper {
      flex-direction: column;
  }

  .social__media--wrap {
      flex-direction: column;
  }
}

@media screen and (max-width: 480px) {
  .footer__link-items {
      padding: 10px;
      margin: 0;
      width: 100%;
  }
}

.menu__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1.5em;
}

.menu {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 14px;
  color: #e3dc2c;
}

.menu-group-heading {
  margin: 0;
  padding-bottom: 1em;
  border-bottom: 2px solid #ccc;
}

.menu-group {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5em;
  padding: 1.5em 0;
}

.menu-item {
  display: flex;
}

.menu-item-image {
  width: 80px;
  height: 80px;
  flex-shrink: 0;
  object-fit: cover;
  margin-right: 1.5em;
}

.menu-item-text {
  flex-grow: 1;
}

.menu-item-heading {
  display: flex;
  justify-content: space-between;
}

@media screen and (min-width: 992px) {
  .menu {
      font-size: 16px;
  }

  .menu-group {
      grid-template-columns: repeat(2, 1fr);
  }

  .menu-item-image {
      width: 125px;
      height: 125px;
  }
}

.contact__container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px 100px;
}

.contact__container:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
      to bottom,
      rgba(17,17,17,0.6) 100%, 
      rgba(0,0,0,0) 0%), 
      url('./pages/images/pic4.jpg');
  background-size: cover;
  z-index: -1;
  filter: blur(5px);
  
}

.contact-box {
  max-width: 650px;
  max-height: 1600px;
  margin-top: 4rem;
  margin-bottom: 12rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  text-align: center;
}

.left {
  height: 100px;
  width: 100px;
  position: relative;
  justify-self: center;
  align-items: center;
  background: url("./pages/images/emailpic.svg") no-repeat center;
  background-size: cover;
}

.right {
  padding: 25px 40px;
}

.contact__container h2 {
  color: #e9e9e9;
  font-size: 2.5rem;
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.contact__container h3 {
  color: #fff;
  justify-self: center;
  align-items: center;
  font-size: 2rem;
  position: relative;
  padding-bottom: 10px;
  margin: 10px;
  text-align: center;
}

.contact__container ul {
  color: #e9e9e9;
  font-size: 1.5rem;
  position: relative;
  padding-bottom: 10px;
  margin: 1em;
}

.contact__container h2:after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: 4px;
  width: 50px;
  border-radius: 2px;
  background-color: #e3dc2c;
}

.contact__container h1 {
  color: #ccc;
  font-size: 1rem;
  margin: 1rem;

}

.field {
  width: 100%;
  padding: 0.5rem 1;
  outline: 2px solid #e9e9e9;
  border: 2px solid rgba(0,0,0,0);
  background-color: rgba(230, 230, 230, 0.6);
  font-size: 1.1rem;
  margin-bottom: 22px;
  transition: .3s;
}

.field:hover {
  background-color: rgba(0,0,0,0.13);
}

.area {
  min-height: 150px;
}

.btn {
  width: 100%;
  padding: 0.5rem 1rem;
  font-size: 1.1rem;
  background-color: #c4c182;
  cursor: pointer;
  outline: none;
  border: none;
  color: #fff;
  transition: .3s;
}

.btn:hover {
  background-color: #f3f0a7;
}

@media screen and (max-width:880px) {
  .contact-box {
      grid-template-columns: 1fr;
  }
  
  .left {
      justify-self: center;
  }
}

